<template>
  <div
    v-if="isContactInfoStep && firstNameField"
    class="name-fields__note"
  >
    <span
      v-text="charsRemaining"
    />&nbsp;<span
      v-text="translations.nameMaxCharacterSuffix"
    />
  </div>
  <span v-else />
</template>

<script>

 /**
 * Vue: Checkout name fields (checkout-name-fields)
 * -----------------------------------------------------------------------------
 *
 */

export default {
  name: 'CheckoutNameFields',
  props: {
    maxChars: {
      type: Number,
      default: 35,
    },
  },
  data() {
    const { checkout } = window.cnvs.strings
    return {
      firstNameField: null,
      lastNameField: null,
      firstNameFieldId: 'checkout_shipping_address_first_name',
      lastNameFieldId: 'checkout_shipping_address_last_name',
      firstNameLength: 0,
      lastNameLength: 0,
      translations: {
        ...checkout,
      },
    }
  },
  computed: {
    charsRemaining() {
      const remainder = this.maxChars - this.firstNameLength - this.lastNameLength
      return remainder < 0 ? 0 : remainder
    },
    isContactInfoStep() {
      return Shopify.Checkout.step === 'contact_information'
    },
  },
  mounted() {
    if (this.isContactInfoStep) {
      this.setFirstNameField()
      this.setLastNameField()
      this.limitLastNameFieldLength()
      this.limitFirstNameFieldLength()
    }
  },
  methods: {
    limitFirstNameFieldLength() {
      const totalChars = this.firstNameField.value.length + this.lastNameField.value.length
      const charsToRemove = totalChars - this.maxChars
      if (charsToRemove > 0) {
        const endCharIndex = this.firstNameField.value.length - charsToRemove
        this.firstNameField.value = this.firstNameField.value.substr(0, endCharIndex >= 0 ? endCharIndex : 0)
      }

      this.firstNameLength = this.firstNameField.value.length
    },
    limitLastNameFieldLength() {
      const totalChars = this.firstNameField.value.length + this.lastNameField.value.length
      const charsToRemove = totalChars - this.maxChars
      if (charsToRemove > 0) {
        const endCharIndex = this.lastNameField.value.length - charsToRemove
        this.lastNameField.value = this.lastNameField.value.substr(0, endCharIndex >= 0 ? endCharIndex : 0)
      }

      this.lastNameLength = this.lastNameField.value.length
    },
    setFirstNameField() {
      this.firstNameField = document.getElementById(this.firstNameFieldId)
      this.firstNameField.setAttribute('maxlength', this.maxChars)
      this.firstNameField.addEventListener('keyup', this.limitFirstNameFieldLength)
      this.firstNameField.addEventListener('change', this.limitFirstNameFieldLength)
    },
    setLastNameField() {
      this.lastNameField = document.getElementById(this.lastNameFieldId)
      this.lastNameField.setAttribute('maxlength', this.maxChars)
      this.lastNameField.addEventListener('keyup', this.limitLastNameFieldLength)
      this.lastNameField.addEventListener('change', this.limitLastNameFieldLength)
    },
  },
}
</script>